.containerWithCard{
  width: 90%;
  margin: 0 auto;
  .container{
    display: flex;
    flex-direction:row;
    max-width: 1280px;
    .cards{
      width: 30%;
      padding-right: 1%;
      display: flex;
      flex-direction: column;
      .card{
        margin-bottom: 10px;
        margin-right: 10px;
        padding: 20px;
        background: white;
        border-radius: 10px;
        color: black;
        font-weight: normal;
      }
    }
    .wrapper {
      position: relative;
      width: 70%;
      padding-top: 56.25%; /* 16:9 Aspect Ratio */
      .iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 20px;
      }
    }

    @media screen and (max-width: 768px) {
      flex-direction: column;
      .cards {
        width: 100%;
        order: 1;
        flex-direction: row;
        column-gap: 10px;
        .card{
          margin-top: 10px;
          margin-right: 0;
          flex: 1;
        }
      }

      .wrapper {
        order: 0;
        position: relative;
        width: 100%;
        margin: 0 auto;
        padding-top: 56.25%; /* 16:9 Aspect Ratio */
        .iframe {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          border-radius: 20px;
        }
      }
    }
  }
}

.Collapse{
  width:80%;
  margin: 0 auto;
  font-size: 20px;
  color: white;
  @media screen and (max-width: 768px) {
    width:90%;
  }
}

.subtitle {
  color: #a8a8a8ff;
  font-size: 36px;
  font-weight: 500;
  line-height: normal;
  margin: 0 0 20px 0;
}

.content {
  color: #ffffffff;
  font-size: 20px;
  font-weight: 400;
  line-height: normal;
  margin: 20px 0;
}

.mainTitle{
  color: white;
  font-size: 64px;
  font-weight: 700;
  margin: 50px 0 30px 0;
  line-height: normal;
  @media screen and (max-width: 768px) {
    font-size: 40px;
    line-height: normal;
  }
}

.payCards {
  display: flex;
  width: 90%;
  margin: 0 auto;
  flex-direction: row;
  column-gap: 40px;
  .card{
    flex: 1;
    border-radius: 20px;
    background: transparent;
    box-shadow: 0 0 6px #39797a;
    min-height: 500px;
    padding: 20px;
    .title{
      font-size: 36px;
      line-height: normal;
      margin: 20px 0 10px 0;
    }
    .subtitle{
        font-size: 24px;
        margin: 0 0 10px 0;
        color:#A8A8A8;
    }
  }
  @media screen and (max-width: 768px) {
    flex-direction: column;
    .card {
      margin-bottom: 20px;
      min-height: 200px;
    }
  }
}

.workflowContainer{
  display: flex;
  width: 100%;
  margin: 0 auto;
  flex-direction: row;
  column-gap: 40px;
  .workflow{
    flex: 1;
  }
  @media screen and (max-width: 768px) {
    flex-direction: column;
    .workflow {
      width: 100%;
      margin-bottom: 20px;
    }
  }
}

.advantageCard{
  display: flex;
  flex: 1;
  border-radius: 10px;
  padding: 20px;
  min-height: 160px;
  align-items: center;
  justify-content: center;
  font-size: 36px;
  line-height: 150%;
  box-shadow:0 0 6px #39797a;
  text-align: center;
  @media screen and (max-width: 768px) {
