  }
}

.advantage{
  width:100%;
  display: flex;
  column-gap:40px;
  @media screen and (max-width: 768px) {
    column-gap:20px;
  }
}
.UserCardContainer{
  display: flex;
  column-gap: 20px;
  margin-bottom: 20px;
  .userCard{
    flex: 1;
    border-radius: 20px;
    background: transparent;
    box-shadow: 0 0 6px #39797a;
    padding: 20px;
    font-size: 16px;
    font-weight: 400;
    text-align: justify;
    .img{
      width: 50px;
      height: 50px;
      border-radius: 25px;
      margin-right: 10px;
    }

  }
  @media screen and (max-width: 500px) {
    flex-direction: column;
    margin-bottom: 0;
    .userCard {
      margin-bottom: 20px;
    }
  }
}

.specialTitle{
  font-size: 36px;
  line-height: normal;
  margin: 20px 0 10px 0;
  background: -webkit-linear-gradient(180deg, #f9d71c, #d4af37);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  display: inline-block;
}
